import React from 'react';

import { Button } from 'antd';
import { Form } from 'antd';
import { Select } from 'antd';


class ContractsFilterForm extends React.Component {
    onSubmit = (e) => {
        e.preventDefault();

        this.props.form.validateFieldsAndScroll(
            (errors, values) => {
                if (errors) {
                    return;
                }

                this.props.onFilterUpdate(values);
            }
        );
    }

    hint = (flags) => {
        const titles = [
            [' ', '', ''],          // 0
            ['  ', '', ''],       // 1
            ['', '', ' (  )'], // 2
            [' ', '', ''],     // 3
            ['  ', '', ''],       // 4
            ['  7 ', '', ' '],        // 5
            ['    3 ', '', ' '], // 6
        ];

        let text = '';
        for (let i = 0; i < titles.length; ++i) {
            const title = titles[i][0];
            const yes = titles[i][1];
            const no = titles[i][2];

            let answer = no;
            if ((flags >> i) & 1) {
                answer = yes;
            }
            
            text += (title + ': ' + answer + '\n');
        }

        return text;
    }

    filterSelectItem = () => {
        let items = [
            {
                key: 'all',
                title: ' ',
                hint: '  ,  ,   ',
            },
            {
                key: 'active',
                title: '  ',
                hint: '   ,\n     ,\n  '
            },
            {
                key: 'to_register',
                title: ' ',
                hint: '    4 ,\n   7,\n   ,   7 '
            },
            {
                key: 'registration_in_progress',
                title: ' ',
                hint: this.hint(0b1),
            },
            {
                key: 'registration_expiring',
                title: ' ,  ',
                hint: this.hint(0b1000001),
            },
            {
                key: 'snils_expiring',
                title: ',  ',
                hint: this.hint(0b11),
            },
            {
                key: 'registered_with_notification_with_snils',
                title: ',  ',
                hint: this.hint(0b10100),
            },
            {
                key: 'to_fire',
                title: ' ',
                hint: this.hint(0b100011),
            },
            {
                key: 'firing_in_progress',
                title: '',
                hint: this.hint(0b1011),
            },
            {
                key: 'firing_expiring',
                title: ',  ',
                hint: this.hint(0b1001011),
            },
            {
                key: 'fired',
                title: '',
                hint: this.hint(0b0011011),
            },
        ];

        let options = items.map(
            item => (
                <Select.Option
                    key={item.key}
                    value={item.key}
                    title={item.hint}
                >
                    {item.title}
                </Select.Option>
            )
        );

        return (
            <Form.Item
                key='filter'
                style={{ marginBottom: 0 }}
                label=':'
            >
                {
                    this.props.form.getFieldDecorator(
                        'filter',
                        { rules: [{ required: true, message: '  !' }] }
                    )(
                        <Select
                            placeholder=''
                            style={{ width: '240px'}}
                        >
                            {options}
                        </Select>
                    )
                }
            </Form.Item>
        );
    }

    submitItem = () => {
        return (
            <Form.Item
                key='submit'
                style={{ marginBottom: 0 }}
            >
                <Button
                    htmlType='submit'
                >
                    
                </Button>
            </Form.Item>
        );
    }

    render() {
        return (
            <Form
                layout='inline'
                onSubmit={this.onSubmit}
            >
                {this.filterSelectItem()}
                {this.submitItem()}
            </Form>
        );
    }
}


const ContractsFilter = Form.create(
    {
        mapPropsToFields(props) {
            let result = {};
            if (props.filter) {
                result.filter = Form.createFormField({ value: props.filter });
            }
            return result;
        }
    }
)(ContractsFilterForm);


export default ContractsFilter;
